@import url("https://cdn.jsdelivr.net/gh/orioncactus/pretendard@v1.3.6/dist/web/variable/pretendardvariable-dynamic-subset.css");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family:
    "Pretendard Variable",
    Pretendard,
    -apple-system,
    BlinkMacSystemFont,
    system-ui,
    Roboto,
    "Helvetica Neue",
    "Segoe UI",
    "Apple SD Gothic Neo",
    "Noto Sans KR",
    "Malgun Gothic",
    "Apple Color Emoji",
    "Segoe UI Emoji",
    "Segoe UI Symbol",
    sans-serif;
}

body {
  background-color: #f8f8f8;
}

a {
  text-decoration: none;
}

ol, ul {
  list-style: none;
}

code {
  font-family: menlo;
}

@media (prefers-color-scheme: light) {
  * {
    color: #333;
  }

  body {
    background-color: #f8f8f8;
  }
}

@media (prefers-color-scheme: dark) {
  * {
    color: #f8f8f8;
  }

  body {
    background-color: #333333;
  }
}